@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
}
.dropdown-container {
  width: 100%;
}
.dropdown-container .dropdown .btn-outline-dark {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
}
.dropdown-container .dropdown .btn-outline-dark:active,
.dropdown-container .dropdown .btn-outline-dark:focus,
.dropdown-container .dropdown .btn-outline-dark {
  background-color: #f8f9fa;
  color: #212529;
}
.dropdown-container .dropdown .btn-outline-dark:hover {
  background-color: #e9ecef;
  color: #212529;
}
.dropdown-container .dropdown .dropdown-menu .dropdown-item {
  cursor: pointer;
}
.dropdown-container .dropdown .dropdown-menu .dropdown-item:hover {
  background-color: #0d6efd;
  color: #fff;
}
.dropdown-container .dropdown .dropdown-menu .hidden {
  display: none;
}
.dropdown-container .dropdown .dropdown-menu .form-control:focus {
  border-color: #dee2e6;
  box-shadow: none;
}
.dropdown-container .selected-items {
  min-height: 2em;
}
.dropdown-container .selected-items > div {
  background-color: #0dcaf0;
  border-radius: 5px;
}
.dropdown-container .selected-items > div .btn-close {
  padding: 0;
  width: 0.65em;
  height: 0.65em;
}
.dropdown-container .selected-items > div .btn-close:focus {
  box-shadow: none;
}

.dropdown-container {
  width: 100%;
}
.dropdown-container .dropdown .dropdown-menu .dropdown-item {
  cursor: pointer;
}
.dropdown-container .dropdown .dropdown-menu .dropdown-item:hover {
  background-color: #0d6efd;
