}
.dropdown-container .selected-items > div {
  background-color: #0dcaf0;
  border-radius: 5px;
}

.c-form {
  height: 500px;
}

.wrapper {
  font-family: "Inter";
}

a {
  color: inherit;
}

button {
  font-family: inherit;
